import { Component, OnInit } from '@angular/core';

import { DatabaseService } from './../../services/database.service';
import { ActivatedRoute } from '@angular/router';
import { NgForm } from '@angular/forms';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.scss']
})
export class ProfileComponent implements OnInit {

  Ref : any;
  stu : any;

  constructor(private stud : DatabaseService, public _route : ActivatedRoute) { }
  
  UpdateStudents(StudentData : NgForm){

    //add to my database
  this.stud.UpdateStudents(StudentData.value, this.Ref);
  }

  ngOnInit(){
    this.Ref = this._route.snapshot.paramMap.get('ref');

    this.stud.EachStudent(this.Ref).subscribe(e => {
      console.log(e);
      this.stu = e;
    });
  }
  DeleteStudents(ref){
    return this.stu.collection('Students').doc(ref).delete().then( results =>{
      console.log('successfully deleted student')
    }).catch ( err => {
      console.log('erroe occured :', err)
    });
  }
}