import { Component, OnInit } from '@angular/core';

import { DatabaseService } from './../../services/database.service';
import { NgForm } from '@angular/forms';
import { Action } from 'rxjs/internal/scheduler/Action';

@Component({
  selector: 'app-landing',
  templateUrl: './landing.component.html',
  styleUrls: ['./landing.component.scss']
})
export class LandingComponent implements OnInit {

  stud : any;
  Ref : any;

  constructor(public _data : DatabaseService) { }

  AddStudents(StudentData : NgForm){

    //add to my database
  this._data.AddStudents(StudentData.value, this.Ref);
  console.log('successfully updated student');
  }

  ngOnInit() {
    this._data.GetStudents().snapshotChanges().subscribe( action => {
      console.log(action);
      this.stud = action;
    });
  }
  }